* {
  box-sizing: border-box;
}
html {
  height: 100vh;
}
body {
  color: white;
  background-image: linear-gradient(145deg, #8a2387, #e94057, #f27121);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  display: block;
  font-family: sans-serif;
  font-size: 20px;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 15px;
}
@media (max-width: 1240px) {
  body {
    font-size: calc((100vw - 320px)/920 * 6 + 12px);
  }
}
@media (max-width: 600px) {
  body {
    font-size: 12px;
    font-size: calc((100vw - 320px)/920 * 6 + 12px);
  }
}
@media (max-width: 600px) {
  body {
    padding: 2%;
  }
}
main {
  margin: 0 auto;
  max-width: 500px;
}
form {
  background: rgba(0, 0, 0, 0.2);
  /* fallback for old browsers */
  border-radius: 5px;
  border: 1px solid #343434;
  padding: 0.4em;
  box-shadow: 0px 10px 40px 5px rgba(0, 0, 0, 0.2);
}
fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
.input-text {
  margin-bottom: 5px;
  position: relative;
  display: flex;
  align-items: center;
}
.input-text label {
  position: absolute;
  left: 10px;
  top: .9em;
  color: rgba(255, 255, 255, 0.5);
  transition: all 0.2s ease-out;
}
.input {
  color: #d5d5d5;
  width: 100%;
  outline: none;
  border-radius: 5px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  background: rgba(0, 0, 0, 0);
  padding: 0.2em 10px;
  margin-top: 0.7em;
  border: none;
  border-bottom: 2px solid rgba(0, 0, 0, 0.15);
  transition: all 0.2s ease-out;
}
.input:not(output):-moz-ui-invalid {
  color: red;
  box-shadow: none;
  border-bottom: 2px solid rgba(255, 0, 13, 0.81);
}
.input:hover {
  background-size: contain;
  background-repeat: no-repeat;
  border-bottom: 2px solid rgba(0, 0, 0, 0.25);
}
.input:focus {
  outline: none;
  transition: all 0.2s ease-out;
  box-shadow: inset 0 -1em 1em -1em rgba(0, 0, 0, 0);
  border-bottom: 2px solid rgba(242, 85, 206, 0.36);
}
.input.not ~ label,
.input:focus ~ label,
.input:valid ~ label {
  transition: all 0.2s ease-out;
  top: -0.2em;
  color: rgba(255, 255, 255, 0.3);
  font-size: 0.8em;
  transform-origin: left;
}
